import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class BookService {

    
    constructor(public http: HttpClient, public storage: Storage) {  }

 
    public getBooks(){
        return new Promise((resolve, reject) => {          
            this.http.get(environment.apiUrl + "/books").subscribe((res) => {
                
                this.storage.set("health_books",res);
                
                resolve(res);
            }, (e) => {
            
                this.storage.get("health_books").then((data) => {
                    if (data){
                        resolve(data);
                    }
                    else{
                        reject(e);
                    }
                }).catch(() => {
                    reject(e);
                })
            
                
            }); 
        });        
    }  
    
    
    public getBook(bookId){
        return new Promise((resolve, reject) => {
            this.storage.get("health_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/books/" + bookId + "?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
            
    
}
